.header {
    position: fixed;
    width: 100%;
    height: 60px;
    background-color: rgb(17,17,17);
    z-index: 10;
}
.header__logo {
    width: 40px;
    margin-left: 10px;
    cursor: pointer;
}
.header__container {
    max-width: 1440px;
    width: 100%;
    margin: auto;
    align-items: center;
    display: flex;
    height: 100%;
    padding: 0 10px;
    box-sizing: border-box;
}
